<template>
  <div>
    <Message :show.sync="msgShow" :type="msgType" :msg="msg" />
    <!-- Â∏ñÂ≠êÂàóË°® Start -->
    <div class="col-md-9 topics-index main-col"><!-- ËøáÊª§Â∏ñÂ≠êÊñπÂºèÂàóË°® Start -->
      <div class="panel panel-default">
        <div class="panel-heading">
          <ul class="list-inline topic-filter">
            <li v-for="item in filters" :key="item.name">
              <router-link
                v-title="item.title"
                :class="{ active: filter === item.filter }"
                :to="`/topics?filter=${item.filter}`"
              >
                {{ item.name }}
              </router-link>
            </li>
          </ul>
          <div class="clearfix"></div>
        </div>
        <div class="panel-body remove-padding-horizontal">
          <ul class="list-group row topic-list">
            <li v-for="article in articles" :key="article.articleId" class="list-group-item">
              <router-link :to="`/articles/${article.articleId}/content`" tag="div" class="reply_count_area hidden-xs pull-right">
                <div class="count_set">
                  <span class="count_of_votes" title="ÊäïÁ•®Êï∞">{{ article.likeUsers ? article.likeUsers.length : 0 }}</span>
                  <span class="count_seperator">/</span>
                  <span class="count_of_replies" title="ÂõûÂ§çÊï∞">{{ article.comments ? article.comments.length : 0 }}</span>
                  <span class="count_seperator">|</span>
                  <abbr class="timeago">{{ article.date | moment('from') }}</abbr>
                </div>
              </router-link>
              <router-link :to="`/${article.uname}`" class="avatar pull-left">
                <img :src="article.uavatar" class="media-object img-thumbnail avatar avatar-middle">
              </router-link>
              <router-link :to="`/articles/${article.articleId}/content`" tag="div" class="infos">
                <div class="media-heading">
                  {{ article.title }}
                </div>
              </router-link>
            </li>
          </ul>
        </div>
        <div class="panel-footer text-right remove-padding-horizontal pager-footer">
          <Pagination :currentPage="currentPage" :total="total" :pageSize="pageSize" :onPageChange="changePage"/>
        </div>
      </div>
    </div><!-- ËøáÊª§Â∏ñÂ≠êÊñπÂºèÂàóË°® End -->
    <!-- Â∏ñÂ≠êÂàóË°® End -->
    <TheSidebar/>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import TheSidebar from '@/components/layouts/TheSidebar';

export default {
  name: 'Home',
  components: {
    TheSidebar
  },
  data() {
    return {
      msg: '',
      msgType: '',
      msgShow: false,
      articles: [],
      filter: 'default', // ÈªòËÆ§ËøáÊª§ÊñπÂºè
      filters: [ // ËøáÊª§ÊñπÂºèÂàóË°®
        { filter: 'default', name: 'Ê¥ªË∑É', title: 'ÊúÄÂêéÂõûÂ§çÊéíÂ∫è' },
        { filter: 'excellent', name: 'Á≤æÂçé', title: 'Âè™ÁúãÂä†Á≤æÁöÑËØùÈ¢ò' },
        { filter: 'vote', name: 'ÊäïÁ•®', title: 'ÁÇπËµûÊï∞ÊéíÂ∫è'},
        { filter: 'recent', name: 'ÊúÄËøë', title: 'ÂèëÂ∏ÉÊó∂Èó¥ÊéíÂ∫è'},
        { filter: 'noreply', name: 'Èõ∂ÂõûÂ§ç', title: 'Êó†‰∫∫ÈóÆÊ¥•ÁöÑËØùÈ¢ò'}
      ],
      total: 0, // ÊñáÁ´†ÊÄªÊï∞
      pageSize: 10, // ÊØèÈ°µÊù°Êï∞
    };
  },
  // ÁªÑ‰ª∂ÂÜÖÁöÑË∑ØÁî±ÂØºËà™ÂÆàÂç´ÔºåÂú®Á°ÆËÆ§Ê∏≤ÊüìËØ•ÁªÑ‰ª∂ÁöÑÂØπÂ∫îË∑ØÁî±ÂâçË∞ÉÁî®„ÄÇ
  // ËØ•ÂÆàÂç´‰∏çËÉΩËÆøÈóÆ thisÔºå‰ΩÜÊàë‰ª¨ÈÄöËøá‰º†‰∏Ä‰∏™ÂõûË∞ÉÁªô nextÔºåÂ∞±ÂèØ‰ª•‰ΩøÁî®‰∏äÈù¢ÁöÑ vm Êù•ËÆøÈóÆÁªÑ‰ª∂ÂÆû‰æã„ÄÇ
  beforeRouteEnter(to, from, next) {
    // Ë∑ØÁî±ÁöÑÂêçÁß∞ÔºåÂØπÂ∫îË∑ØÁî±ÈÖçÁΩÆ‰∏≠ÁöÑ name
    const fromName = from.name;
    const logout = to.params.logout;

    // Á°ÆËÆ§ÂØºËà™
    next(vm => {
      // ÈÄöËøá vm ÂèÇÊï∞ËÆøÈóÆÁªÑ‰ª∂ÂÆû‰æãÔºåÂ∑≤ÁôªÂΩïÊó∂ÔºåËØÑ‰º∞Ë∑ØÁî±ÂêçÁß∞
      if (vm.$store.state.auth) {
        switch (fromName) {
          // Â¶ÇÊûú‰ªéÊ≥®ÂÜåÈ°µÈù¢Ë∑≥ËΩ¨ËøáÊù•
          case 'Register':
            vm.showMsg('Ê≥®ÂÜåÊàêÂäü');
            break;
          case 'Login':
            vm.showMsg('ÁôªÂΩïÊàêÂäü');
            break;
        }
      } else if (logout) {
        vm.showMsg('Êìç‰ΩúÊàêÂäü');
      }

      // Á°ÆËÆ§Ê∏≤ÊüìËØ•ÁªÑ‰ª∂ÁöÑÂØπÂ∫îË∑ØÁî±Êó∂ÔºåËÆæÁΩÆÁõ∏ÂÖ≥Êï∞ÊçÆ
      vm.setDataByFilter(to.query.filter)
    });
  },
  computed: {
    ...mapState([
      'auth',
      'user'
    ]),
    // ÂΩìÂâçÈ°µÔºå‰ªéÊü•ËØ¢ÂèÇÊï∞ page ËøîÂõû
    currentPage() {
      return parseInt(this.$route.query.page) || 1
    }
  },
  watch: {
    // È¶ñÈ°µÈÄÄÂá∫Êó∂ `beforeRouteEnter` ‰∏ç‰ºöË¢´Ë∞ÉÁî®
    // ÁõëÂê¨‰∏ã auth ÁöÑÂÄº
    auth(newValue) {
      if (!newValue) {
        this.showMsg('Êìç‰ΩúÊàêÂäü');
      }
    },
    '$route'(to) {
      this.setDataByFilter(to.query.filter)
    }
  },
  methods: {
    showMsg(msg, type = 'success') {
      this.msg = msg;
      this.msgType = type;
      this.msgShow = true;
    },
    setDataByFilter(filter = 'default') {
      // ÊØèÈ°µÊù°Êï∞
      const pageSize = this.pageSize;
      // ÂΩìÂâçÈ°µ
      const currentPage = this.currentPage;
      // ËÆæÁΩÆÂΩìÂâçËøáÊª§ÊñπÂºè‰∏∫Êü•ËØ¢ÂèÇÊï∞ÁöÑ filter
      this.filter = filter;
      // ËÆæÁΩÆÊñáÁ´†ÂàóË°®‰∏∫ËøáÊª§ÂêéÁöÑÊâÄÊúâÊñáÁ´†
      const allArticles = this.$store.getters.getArticlesByFilter(filter);
      // ÊñáÁ´†ÊÄªÊï∞
      this.total = allArticles.length;
      // ÂΩìÂâçÈ°µÁöÑÊñáÁ´†
      this.articles = allArticles.slice(pageSize * (currentPage - 1), pageSize * currentPage);
    },
    // ÂõûË∞ÉÔºåÁªÑ‰ª∂ÁöÑÂΩìÂâçÈ°µÊîπÂèòÊó∂Ë∞ÉÁî®
    changePage(page) {
      // Âú®Êü•ËØ¢ÂèÇÊï∞‰∏≠Ê∑∑ÂÖ• page, Âπ∂Ë∑≥ËΩ¨Âà∞ËØ•Âú∞ÂùÄ
      // Ê∑∑ÂÖ•ÈÉ®ÂàÜÁ≠â‰ª∑‰∫é Object.assgin({}, this.$route.query, { page: page })
      this.$router.push({ query: { ...this.$route.query, page } });
    }
  }
};
</script>

